body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  margin: 0;
  padding: 0;
  height: 100vh;
  overflow: hidden;
  position: relative;
  z-index: 0;
}

#particles-js {
  position: absolute;
  width: 100vw;
  height: 100%;
  background-color: #fff;
  z-index: -1;
}


.circle-2 {
  overflow: hidden;
  content: "";
  top: calc(50%);
  left: -150px;
  width: 663px;
  height: 581px;
  position: absolute;
  background: linear-gradient(rgba(11, 88, 255, 1), rgba(255, 255, 255, 1));
  z-index: -1;
  border-radius: 50%;
}

.message{
  position: absolute;
  visibility: hidden;
}

@media (max-width: 500px) {

  #root {
    margin: 0;
    padding: 0;
    flex-direction: column;
    height: 100vh;
    background-color: white;
    overflow: hidden;
    position: relative;
  }

  .circle-2 {
    display: flex;
    width: 150%;
    height: 800px;
    top: -58%;
    left: -25%;
    position: absolute;
    background: linear-gradient(rgba(255, 255, 255, 1),rgba(255, 255, 255, 1), rgba(11, 88, 255, 1));

  }

  .message{
    position: absolute;
    right: 20px;
    top: 50px;
    visibility: visible;
    z-index: 1;
    animation: message 3s;
  }

  @keyframes message {
    0% {
      transform: translateY(-100%);
    }

    80% {
      transform: translateY(0%);
    }
    
  }

  

}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}