.navigation {

    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    background-color: #fff;
    position: relative;
    width: 100%;
    height: 120px;
    padding: 20px;

}

.navigation-img {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: inherit;
    position: relative;
}

.navigation-img img {

    position: absolute;
    left: 70px;
    width: 100px;
    height: 100px;
    border-radius: 164px;
    margin: auto;
    background: white;
}

.navigation-list {
    display: flex;
    flex-direction: row;
    list-style: none;
    align-items: center;
    color: #000000;
    width: 100%;
    height: inherit;
}


.navigation-list .list-home-1 li {
    padding: 15px;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: bold;
}

.navigation-list .list-home-1 li:hover {
    padding: 15px;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: bold;
    background-color: #4C8EF1;
    color: white;
    border-radius: 10px;
}

.list-home-1 {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    list-style: none;
    align-items: center;
    width: 100%;
    position: relative;

}

.list-home-1 li:first-child {

    background-color: #4C8EF1;
    color: white;
    border-radius: 10px;
}


/*Contact*/

.navigation-contact {
    background: transparent;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: inherit;
    position: relative;

}

.navigation-contact .img-contact {
    list-style: none;
    justify-content: center;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: inherit;
    position: relative;
    z-index: 0;
}

.navigation-contact .img-contact li {
    z-index: 1;
    margin: 15px;

}

.circle {
    content: "";
    top: -200px;
    left: calc(70%);
    width: 663px;
    height: 581px;
    position: absolute;
    background: linear-gradient(rgba(255, 255, 255, 1), rgba(11, 88, 255, 1));
    z-index: 0;
    border-radius: 50%;

}

.navigation-contact h2 {
    visibility: hidden;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: bold;
    color: #000000;
    position: absolute;
    top: 0;
    left: 0;
    padding: 20px;
    z-index: 1;
}

@media (max-width: 500px) {
    .navigation {
        width: 100%;
        --size:var(width);
        display: flex;
        flex-direction: column;
        position: absolute;
        bottom: 0;
        padding: 0;
        background: #fff;
        animation: caja_2 normal 3s;
    }

    .navigation-contact {
        display: flex;
        flex-direction: column;
        height: 100%;
        position: absolute;
        bottom: 20px;
    }


    .navigation-contact h2 {
        width: auto;
        visibility: visible;
        position: absolute;
        left: calc(50%);
        transform: translateX(-50%);
        margin: 0;
        padding: 0;
        font-family: 'Inter', sans-serif;
        font-weight: 600;
        color: #805757;
        font-size: 20px;
        font-weight: 700;
    }

    .navigation-contact .img-contact {
        margin-top: 10px;
        padding: 0;
        margin: 0;

    }

    /*
    .navigation-contact .img-contact li {
       width: 25px;
    }
*/
    .circle {
        display: none;
    }


    .navigation-img img{
       
        visibility: visible;
        position: absolute;
        top: -460px;
        left: calc(50%);
        transform: translateX(-50%);
        animation: none;

    }

    .navigation-list {
        display: none;
    }

    .navigation-contact {
        display: flex;
        flex-direction: row;
    }

    .navigation-contact .img-contact .small-icon {
        width: 25px;
        height: 25px;
    }

}